import {
  FormatToPercent,
  ConvertNumberToPercentage,
  FormattedCurrentValue
} from "./scripts/functions";

// * DOM Elements
const referenceElement = document.getElementById("reference-element");
const percentageScrolled = document.getElementById("percentage-scrolled");
const scroller = document.getElementById("scroller");

// * Element Measurements
const documentElement = document.documentElement;
let windowHeight = documentElement.clientHeight;
const referenceElementHeight = referenceElement.clientHeight;
const scrollerHeight = scroller.clientHeight;

// * Observer
const scrollStream = new MutationObserver(callback);
const streamOptions = {
  childList: true,
  // attributes: true,
  characterData: true
  // subtree: true,
  // attributeFilter: ["one", "two"],
  // attributeOldValue: false,
  // characterDataOldValue: false
};

function callback(mutations) {
  // console.log("mutations", mutations[0].target.innerText);
}


// if (window) {
scrollStream.observe(percentageScrolled, streamOptions);
// }

const handleScrollEvent = function scrollHandler(
  size
) {
  const currentValue = referenceElement.getBoundingClientRect().y;
  const number = FormatToPercent(
    ConvertNumberToPercentage(FormattedCurrentValue(currentValue), size)
  );
  const progress = FormattedCurrentValue(number);
  if (progress > 2 && progress < 98.0) {
    scroller.style.top = progress + "%";
    percentageScrolled.innerText = progress;
    // * CSS
    if (scroller.classList.contains("snap")) {
      scroller.classList.remove("snap");
    }
    return;
  }
  if (progress > 0 && progress < 2) {
    scroller.style.top = "0%";
    percentageScrolled.innerText = 0;
    // * CSS
    scroller.classList.add("snap");
    return;
  }
  if (progress > 98 && progress < 100) {
    scroller.style.top = "100%";
    percentageScrolled.innerText = 100;
    // * CSS
    scroller.classList.add("snap");
    return;
  }
};



// * Script
if (window) {
  // todo  : use resive observer
  window.addEventListener("resize", () => {
    windowHeight = document.documentElement.clientHeight;
    // console.log("windowHeight", windowHeight);
  });

  let totalHeight = referenceElement.clientHeight - windowHeight;
  let negOrPos = Math.sign(referenceElement.getBoundingClientRect().y);

  // let isScrolling = false;
  window.addEventListener(
    "scroll",
    e => {
      // isScrolling = true;
      negOrPos = Math.sign(referenceElement.getBoundingClientRect().y);

      if (negOrPos === -1) {
        handleScrollEvent(totalHeight);
      } else {
        return;
      }
    },
    { passive: true }
  );
}
